.myskills {
  display: grid;
  place-items: center;
  gap: 2rem;
  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 5rem;
    &__html,
    &__css,
    &__react,
    &__javascript,
    &__php,
    &__python {
      & > img {
        width: 10rem;
        height: 10rem;
      }
    }
  }
}
@media only screen and (min-width: 768px) {
  .myskills {
    &__content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;
      flex-wrap: wrap;
      gap: 10rem;
      &__html,
      &__css,
      &__react,
      &__javascript,
      &__php,
      &__python {
        & > img {
          width: 10rem;
          height: 10rem;
        }
      }
    }
  }
}
