.aboutMe {
  display: grid;
  place-items: center;
  margin-top: 5rem;
  &__content {
    display: grid;
    place-items: center;
    &__Image {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      & > img {
        border-radius: 50%;
        object-position: 52% 44%;
        object-fit: none;
        // background-position: top;
        width: 20rem;
        height: 20rem;
      }
    }
    &__textContent {
      text-align: center;
      & > p {
        text-align: center;
      }
    }
    &__text {
      text-align: center;
      line-height: 1.5rem;
      padding: 1rem;
      font-size: 1.125rem;
      background-color: rgb(202, 201, 201);
      border: solid 1px grayscale($color: #4b4a4a);
      width: 90%;
    }
  }
}
@media only screen and (min-width: 768px) {
  .aboutMe {
    &__content {
      width: 90%;
      display: flex;
      justify-content: center;
      gap: 5rem;
    }
  }
}
