.card {
  display: grid;
  place-items: center;
  background-color: transparent;
  height: 300px;
  width: 300px;
  perspective: 1000px;
  &:hover &__content {
    transform: rotateY(180deg);
  }
  &__content {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;

    &__front,
    &__back {
      position: absolute;
      width: 100%;
      height: 100%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
    }
    &__front {
      & > img {
        height: 200px;
        width: 200px;
      }
    }

    &__back {
      transform: rotateY(180deg);
      border: solid 2px black;
      background-color: grey;
    }
  }
}
@media only screen and (min-width: 768px) {
  .card {
    display: grid;
    place-items: center;
    &__content {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
