.myProjects {
  &__text {
    text-align: center;
  }
  &__contents {
    align-self: center;
    gap: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    & > img {
    }
  }
}
@media only screen and (min-width: 768px) {
  .myProjects {
    display: grid;
    place-items: center;
    &__contents {
      gap: 5rem;
      display: flex;
      align-items: center;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-self: center;
    }
  }
}
